	module.exports = {"data":{"wordpressPost":{"title":"About The Beauty Insider","content":"<p class=\"p1\"><span class=\"s1\">The Beauty Insider is the creation of beauty director Kelly Baker.</span></p>\n<p class=\"p1\"><span class=\"s1\">Kelly is one of the best-known and respected beauty directors in Australia. She is currently the Beauty Expert for the Network Nine websites 9Style and 9Honey. She also regularly appears on the Nine Network’s A Current Affair and Today Extra with David Campbell and Sonia Kruger to talk all things beauty and style.</span></p>\n<p class=\"p1\"><span class=\"s1\">She was the Beauty and Health Director at The Australian Women’s Weekly from 2009 to 2016. She then held the role of Editor of the best-selling fashion and beauty magazine SHOP Til You Drop. From there she took on the much-respected gig of Beauty Editor at News Corps’ body+soul before eventually moving to the Nine group.</span></p>\n<p class=\"p1\"><span class=\"s1\">Kelly has written about beauty and style for virtually every publication in the country including Vogue Australia, The Sydney Morning Herald, New Idea, TV Week, New Woman and Cosmopolitan. She’s known for her warm, friendly style and deep understanding of both beauty and fashion.</span></p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/02/01081915/Kelly_0033-e1488430953887.jpg"},"categories":[{"slug":"beauty-talk","name":"Beauty Talk"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"8067f718-ca4d-5bed-8eef-7c4ab94cfee4"}}

